import React, { useState, useEffect } from 'react';

import axios from 'axios';
import './PageAbout.css';
import Zoom from 'react-reveal/Zoom';

function PageAbout(props) {


	const [contact, setContact] = useState([]);
	useEffect(() => {
		axios({
			method: 'GET',
			url: 'http://192.168.0.10:8000/api/main'
		}).then((response) => {
			setContact(response.data);
		});
	}, []);



	return (
		<Zoom>
			<div className="PageAbout">
				<br />
				{/* <h3>Контакты</h3> */}
				{contact.map((c) => (c.id === 8)?(<h3>{c.name}</h3>) : null)}

				<br />
				<div>
					<li>
						<span>тел.: +7(495)775-27-37, +7(495)775-27-47</span>
					</li>
					<li>
						<span>e-mail: info@gk-pro.ru</span>
					</li>
					<li>
						<span>www.gk-kontur.pro</span>
					</li>
				</div>
				<br />
				<br />
				<br />
				<h3>Время работы</h3>
				<br />
				<div>
					Понедельник - Пятница: 10:00 до 19:00
					<br />
					Суббота - Воскресенье - Выходной
				</div>
				<br />
			</div>
		</Zoom>
	);
}

export default PageAbout;
